import React, { Component } from 'react';
import * as _ from 'lodash';
var countries = require('./icons/countries');
var finalResult = require('./result');

const r16Point = 20;
const quarterPoint = 30;
const semiPoint = 40;
const finalPoint = 50;
const champPoint = 100;

class Participants extends Component {
    totalPoints;
    constructor(props) {
        super(props);
        console.log(this.props);
        this.state = {
          displayBracket: false
        }
        this.totalPoints = this.calculateR16Points() + this.calculateQuarterPoints() + this.calculateSemiPoints() + this.calculateFinalPoints() + this.calculateWinnerPoints();
      }


      calculateR16Points() {
        var r16PointLocal = 0;
        _.filter(finalResult.default.r16,(value,key) => {
          // if matched in exact position then give full point
          if (this.props.participant.brackets.r16[key] == value ) {
              r16PointLocal += r16Point;
          // if partial match give half point 
          } else {
            if (_.includes(this.props.participant.brackets.r16,value)) {
              r16PointLocal += r16Point/2;
            }
          }
        });
        return r16PointLocal;
      }

      calculateQuarterPoints() {
        var localPoint = 0;
        _.filter(finalResult.default.quarter,(value,key) => {
          // if matched in exact position then give full point
          if (this.props.participant.brackets.quarter[key] == value ) {
              localPoint += quarterPoint;
          // if partial match give half point 
          } else {
            if (_.includes(this.props.participant.brackets.r16,value)) {
              localPoint += quarterPoint/2;
            }
          }
        });
        return localPoint;
      }

      calculateSemiPoints() {
        var localPoint = 0;
        _.filter(finalResult.default.semi,(value,key) => {
          // if matched in exact position then give full point
          if (this.props.participant.brackets.semi[key] == value ) {
              localPoint += semiPoint;
          // if partial match give half point 
          } else {
            if (_.includes(this.props.participant.brackets.semi,value)) {
              localPoint += semiPoint/2;
            }
          }
        });
        return localPoint;
      }

      calculateFinalPoints() {
        var localPoint = 0;
        _.filter(finalResult.default.final,(value,key) => {
          // if matched in exact position then give full point
          if (this.props.participant.brackets.final[key] == value ) {
              localPoint += finalPoint;
          // if partial match give half point 
          } else {
            if (_.includes(this.props.participant.brackets.final,value)) {
              localPoint += finalPoint/2;
            }
          }
        });
        return localPoint;
      }

      calculateWinnerPoints() {
        if (this.props.participant.brackets.winner == finalResult.default.winner) {
          return champPoint;
        } else {
          return 0;
        }
      }
    
      toggleBracket = () => {
        this.setState({
          displayBracket : !this.state.displayBracket
        });
      }

      generateR16() {
        var expr16=[];
        for(var i=1;i<=16;i++) {
          expr16.push(
            <div className='match'>
            <div><img className="App-logo" src={countries.default.get(this.props.participant.brackets.r16['r'+i])} /> {this.props.participant.brackets.r16['r'+i]} {this.props.participant.brackets.r16['r'+i+'p']} </div>
            <div><img className="App-logo" src={countries.default.get(this.props.participant.brackets.r16['r'+(++i)])} /> {this.props.participant.brackets.r16['r'+(++i)+'p']} </div>
          </div>
          );
        }
        return expr16;
      }

    render () {
        return (
            <div className='participants'>
                <div className='header' onClick={this.toggleBracket}>
                    <div className='name'>{this.props.participant.name}</div>
                    <div className='location'>{this.props.participant.location}</div> 
                    <div className='points'>{this.totalPoints}</div>
                </div>
            { this.state.displayBracket && <div className="brackets">
                  <div className={`r16 rows`}>
                    <h1>Round of 16 ({this.props.participant.r16total})</h1>
                    <div className='match'>
                      <div><img className="App-logo" src={countries.default.get(this.props.participant.brackets.r16.r1)} /> {this.props.participant.brackets.r16.r1} {this.props.participant.brackets.r16.r1p}</div>
                      <div><img className="App-logo" src={countries.default.get(this.props.participant.brackets.r16.r2)} /> {this.props.participant.brackets.r16.r2} {this.props.participant.brackets.r16.r2p}</div>
                    </div>
                    <div className="match">
                      <div><img className="App-logo" src={countries.default.get(this.props.participant.brackets.r16.r3)} /> {this.props.participant.brackets.r16.r3} {this.props.participant.brackets.r16.r3p}</div>
                      <div><img className="App-logo" src={countries.default.get(this.props.participant.brackets.r16.r4)} /> {this.props.participant.brackets.r16.r4} {this.props.participant.brackets.r16.r4p}</div>
                    </div>
                    <div className="match">
                      <div><img className="App-logo" src={countries.default.get(this.props.participant.brackets.r16.r5)} /> {this.props.participant.brackets.r16.r5} {this.props.participant.brackets.r16.r5p} </div>
                      <div><img className="App-logo" src={countries.default.get(this.props.participant.brackets.r16.r6)} /> {this.props.participant.brackets.r16.r6} {this.props.participant.brackets.r16.r6p}</div>
                    </div>
                    <div className="match">
                      <div><img className="App-logo" src={countries.default.get(this.props.participant.brackets.r16.r7)} /> {this.props.participant.brackets.r16.r7} {this.props.participant.brackets.r16.r7p} </div>
                      <div><img className="App-logo" src={countries.default.get(this.props.participant.brackets.r16.r8)} /> {this.props.participant.brackets.r16.r8} {this.props.participant.brackets.r16.r8p} </div>
                    </div>
                    <div className="match">
                      <div><img className="App-logo" src={countries.default.get(this.props.participant.brackets.r16.r9)} /> {this.props.participant.brackets.r16.r9} {this.props.participant.brackets.r16.r9p}</div>
                      <div><img className="App-logo" src={countries.default.get(this.props.participant.brackets.r16.r10)} /> {this.props.participant.brackets.r16.r10} {this.props.participant.brackets.r16.r10p}</div>
                    </div>
                    <div className="match">
                      <div><img className="App-logo" src={countries.default.get(this.props.participant.brackets.r16.r11)} /> {this.props.participant.brackets.r16.r11} {this.props.participant.brackets.r16.r11p}</div>
                      <div><img className="App-logo" src={countries.default.get(this.props.participant.brackets.r16.r12)} /> {this.props.participant.brackets.r16.r12} {this.props.participant.brackets.r16.r12p}</div>
                    </div>
                    <div className="match">
                     <div> <img className="App-logo" src={countries.default.get(this.props.participant.brackets.r16.r13)} /> {this.props.participant.brackets.r16.r13} {this.props.participant.brackets.r16.r13p}</div>
                      <div><img className="App-logo" src={countries.default.get(this.props.participant.brackets.r16.r14)} /> {this.props.participant.brackets.r16.r14}{this.props.participant.brackets.r16.r14p} </div>
                    </div>
                    <div className="match">
                      <div><img className="App-logo" src={countries.default.get(this.props.participant.brackets.r16.r15)} /> {this.props.participant.brackets.r16.r15} {this.props.participant.brackets.r16.r15p}</div>
                      <div> <img className="App-logo" src={countries.default.get(this.props.participant.brackets.r16.r16)} /> {this.props.participant.brackets.r16.r16} {this.props.participant.brackets.r16.r16p}</div>
                    </div>
                  </div>
                  <div className='quarter'>
                    <h1>Quarter Final ({this.calculateQuarterPoints()})</h1>
                    <div className="match">
                      <div><img className="App-logo" src={countries.default.get(this.props.participant.brackets.quarter.q1)} /> {this.props.participant.brackets.quarter.q1}</div>
                      <div><img className="App-logo" src={countries.default.get(this.props.participant.brackets.quarter.q2)} /> {this.props.participant.brackets.quarter.q2}</div>
                    </div>
                    <div className="match">
                    <div><img className="App-logo" src={countries.default.get(this.props.participant.brackets.quarter.q3)} /> {this.props.participant.brackets.quarter.q3}</div>
                      <div><img className="App-logo" src={countries.default.get(this.props.participant.brackets.quarter.q4)} />{this.props.participant.brackets.quarter.q4}</div>
                    </div>
                    <div className="match">
                    <div><img className="App-logo" src={countries.default.get(this.props.participant.brackets.quarter.q5)} /> {this.props.participant.brackets.quarter.q5}</div>
                    <div><img className="App-logo" src={countries.default.get(this.props.participant.brackets.quarter.q6)} /> {this.props.participant.brackets.quarter.q6}</div>
                    </div>
                    <div className="match">
                    <div><img className="App-logo" src={countries.default.get(this.props.participant.brackets.quarter.q7)} />{this.props.participant.brackets.quarter.q7}</div>
                    <div><img className="App-logo" src={countries.default.get(this.props.participant.brackets.quarter.q8)} />{this.props.participant.brackets.quarter.q8}</div>
                    </div>
                  </div>
                  <div className='semi'>
                    <h1>Semi Final ({this.calculateSemiPoints()})</h1>
                    <div className="match">
                    <div><img className="App-logo" src={countries.default.get(this.props.participant.brackets.semi.s1)} />{this.props.participant.brackets.semi.s1}</div>
                    <div><img className="App-logo" src={countries.default.get(this.props.participant.brackets.semi.s2)} />{this.props.participant.brackets.semi.s2}</div>
                    </div>
                    <div className="match">
                    <div><img className="App-logo" src={countries.default.get(this.props.participant.brackets.semi.s3)} />{this.props.participant.brackets.semi.s3}</div>
                    <div><img className="App-logo" src={countries.default.get(this.props.participant.brackets.semi.s4)} />{this.props.participant.brackets.semi.s4}</div>
                    </div>
                  </div>
                  <div className='final'>
                    <h1>Final ({this.calculateFinalPoints()})</h1>
                      <div className="match">
                      <div><img className="App-logo" src={countries.default.get(this.props.participant.brackets.final.f1)} />{this.props.participant.brackets.final.f1}</div>
                      <div><img className="App-logo" src={countries.default.get(this.props.participant.brackets.final.f2)} />{this.props.participant.brackets.final.f2}</div>
                      </div>
                  </div>
                  <div className='winner'>
                    <h1>Winner ({this.calculateWinnerPoints()})</h1>
                    <img className="App-logo" src={countries.default.get(this.props.participant.brackets.winner)} />
                  </div>
              </div>
              }
          </div>
        );
    }
}
 export default Participants;


